*{
  font-family: Arial, Helvetica, sans-serif;
}
body{
  padding: 60px;
}

input{
  width: 100%;
  color: grey;
  font-size: 1.2rem;
  border-radius: 0.3rem;
  border: 1px solid green;
}
.card{
  align-content: center;
  border:1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}
.card p{
  text-align: center;
  max-height: 170px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}